html {
  height: 100%;
  text-align: center;
}

body {
  margin: 0;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.clock-face {
  box-sizing: border-box;
  width: 300px;
  height: 300px;
  border-radius: 150px;
  border: 15px solid black;
  position: relative;
  display: flex;
  align-items: center;
  justify-content:center;
}

.hand {
    transition: all 100ms cubic-bezier(1, 0.25, 0.63, 1.85);
    position: absolute;
    transform-origin: 50% 100%;
    background-color: black;
}

.second-hand {
  width: 5px;
  height: 120px;
  background-color: red;
  border-radius: 3px;
}

.minute-hand {
  width: 7px;
  height: 100px;
  border-radius: 4px;
}

.hour-hand {
  width: 9px;
  height: 80px;
  border-radius: 5px;
}